// Poster custom web component
import { LitElement, css, html } from "lit";

export class Poster extends LitElement {
    static properties = {
        src: { type: String }
    };

    constructor() {
        super();
        this.src = "";
    }

    render() {
        return html`
            <img src=${this.src} alt="poster" />
        `;
    }
}
customElements.define('show-poster', Poster);